.item_card{
    display: none;
}


.title {
    padding: 0.2em;
    width: 75%;
    border-radius: inherit;
    background-color: inherit;
}
.stock {
    padding: 0 0.4em;
    border-bottom-right-radius: inherit;
    text-align: right;
    background-color: inherit;
    font-size: 0.9em;
}

.wrapper_img {
    width: 100%;
    height: 50%;
    text-align: center;

}

.wrapper_img img {
    max-width: 100%;
    height: auto;
    background-color: beige;
    border-radius: 1em;
    border: 1px solid var(--border);
    object-fit: cover;
}
